import React, { useState } from 'react'
import { djangoLookup } from '../../functions/lookup'
import EventWaiting from './EventWaiting'
import EventCurrent from './EventCurrent'
import Feedback from './Feedback'
import {event} from '../config'
import ContainerTemplate from '../templates/ContainerTemplate'

function Event(props) {
    const [eventDisplay, setEventDisplay] = useState(false)
    const [feedbackDisplay, setFeedbackDisplay] = useState(true)

    let eventComponent
    if(eventDisplay && !feedbackDisplay) {event.tabs[1] = event.tabs[1]} 
    else if(eventDisplay && feedbackDisplay) {event.tabs[1] = event.tabs[2]} 
    else if(!eventDisplay && feedbackDisplay) {event.tabs[1] = event.tabs[3]}
    event.tabs[1].component = eventComponent

    const handleFormSubmitButton = (event) => {
        event.preventDefault()
        const formEl = document.getElementById("register-form")
        const formData = new FormData(formEl)
        let profileData = {}

        formData.forEach((value, key) => {
            profileData[key] = value
        })
    }
    return <ContainerTemplate tabData={event} handleFormSubmitButton={handleFormSubmitButton} showButtons={false} />
}

export default Event