import React from 'react'
import {PersonForm} from '../templates/Forms'
import $ from 'jquery'
import { djangoLookup } from '../../functions/lookup'
import {preference} from '../config'

function Preference(props) {
    const hobbies = new Array(preference.hobbies.n_hobbies).map((hobby, index) => {
        return {name: "hobby" + index, displayName: "Hobby " + index, type: "dropwdown"}
    })

    const handleFormSubmit = (event) => {
        event.preventDefault()
        const formEl = document.getElementById("matching-form")
        const formData = new FormData(formEl)
        let profileData = {}

        formData.forEach((value, key) => {
            profileData[key] = value
        })
        for(let i = 0; i < preference.hobbies.n_hobbies; i++) {
            profileData['hobby' + i] = $('#hobby' + i + ' option:selected').val()
        }
        console.log(profileData) 
        djangoLookup("POST", "/matching/update/", profileData, (response, status) => {
            console.log(response)
        })
    }

    return <div>
        <form id="matching-form" method="POST" onSubmit={handleFormSubmit}>
            <PersonForm entries={hobbies} title={preference.hobbies.subtitle} />
            <PersonForm entries={preference.language.entries} title={preference.language.subtitle} />
            <PersonForm entries={preference.studies.entries} title={preference.studies.subtitle} />
            <button type="submit" className="btn btn-primary">Submit</button>
        </form>
    </div>
}

export default Preference